            <li class="grid grid-cols-12 border-b border-b-[#aeaeae] py-1" v-if="isDisplayHelperRetrieveInDailyReport && !(item.currentDay.helperRetrievePoint === 0 && item.tillNow.helperRetrievePoint === 0)">
              <div class="col-span-6">互助獲得(點)點數/換算金額</div>
              <div class="col-span-3">
