                <template v-if="priceDisplayOnDailyReportDefault.isHelperPointPriceDisplay">
                  / <span v-to-fixed> {{isUsingMathRoundInDesignerDailyReport ? Math.round(item.currentDay.helperRetrievePrice) :  item.currentDay.helperRetrievePrice}}</span>
                </template>
                </div>
              <div class="col-span-3">
